// 
$font-primary: "Roboto", sans-serif;
$font-secondary: "Roboto Cond", sans-serif;
$font-regular: 400;
$font-medium: 500;
$font-bold: 700;
$font-size-min: 16px;
$font-form-size: 16px;
$font-size-modal: 14px;
$font-size-examples: 14px;
$font-form-lineheight: 18px;
$font-color: #000000;
$font-color-secondary: #00659e;
$font-color-additional: #303030;
$font-color-header: #544f4f;
$font-color-footer: #032233;
$font-color-inverted: #ffffff;
//
$input-border-color: #acacac;
$accordion-underline: #acacac;
$success-color: #11c11c;
$error-color: #c11111;
//
$bg-color-primary: #ffffff;
$bg-color-secondary: #00659e;
$bg-color-additional: #f5f5f5;
$bg-color-modal: #ffb820;
$bg-color-btn: #00659e;
$bg-color-btn-secondary: #ffb820;

//  / - (     "mobile first")
$min-width-mobile: 280px;
$min-tablet: 768px;
$min-desktop: 1190px;

// CSS
:root {
  --min-width: #{$min-width-mobile};                  /*        */
  --font-color-basic: #{$font-color};                 /*   */
  --font-color-inv: #{$font-color-inverted};          /*    */
  --font-color-secondary: #{$font-color-secondary};   /*     */
  --bg-color-basic: #{$bg-color-primary};             /*     */
  --bg-color-secondary: #{$bg-color-secondary};       /*     */
  --bg-color-modal: #{$bg-color-modal};               /*      */
  --font-color-additional: #{$font-color-additional}; /*      */
  --header-color-secondary: #{$font-color-header};    /*           ""   --header-bg-color: #{$bg-color-primary};           //  "" */
  --main-color-secondary: #{$font-color-header};      /*       */
  --main-bg-color-additional: #{$bg-color-additional};/*        */
  --footer-color-seconadary: #{$font-color-footer};   /*     "" */
  --footer-bg-color: #{$bg-color-additional};         /*  "" */
  --btn-bg-basic: #{$bg-color-btn};                   /*    */
  --btn-bg-secondary: #{$bg-color-btn-secondary};     /*    */
  --form-text-size: #{$font-form-size};               /*     */
  --form-line-height: #{$font-form-lineheight};       /*      */
  --form-input-border: #{$input-border-color};        /*        */
  --accordion-underline-color: #{$accordion-underline};/*      */
}
